<template>
  <div>
    <v-card-text>
      <v-row :key="index" v-for="(item, index) of items">
        <v-col class="wrap-remove" cols="1">
          <v-checkbox v-model="item.enable" />
        </v-col>
        <v-col cols="3">
          <v-text-field v-model="item.key" label="Key" ></v-text-field>
        </v-col>
        <v-col cols="4">
          <v-text-field
            :type="item.type === 'string' ? '': item.type"
            v-if="item.type === 'string' || item.type === 'number' || item.type === 'password'"
            v-model="item.value"
            label="Value" ></v-text-field>
          <v-combobox
            @input="onChangeValue(item)"
            v-if="item.type === 'variable'"
            style="text-align: left"
            v-model="item.value" :items="valueItems"
            :label="'Value'" persistent-hint></v-combobox>
          <v-select
            :label="'Value'"
            v-if="item.type === 'boolean'"
            v-model="item.value" :items="['true', 'false']"></v-select>
          <v-select
            :label="'Value'"
            v-if="!defaultTypes.includes(item.type)"
            v-model="item.value" :items="dataItems(item.type)"></v-select>
        </v-col>
        <v-col cols="3">
          <v-select @change="(v) => onChangeType(v, item)" v-model="item.type" :items="typeItems"></v-select>
        </v-col>
        <v-col class="wrap-remove" cols="1">
          <v-btn icon @click="clickRemove(index)">
            <v-icon>mdi-close-circle</v-icon>
          </v-btn>
        </v-col>
      </v-row>
    </v-card-text>
    <v-card-actions>
      <v-btn x-small class="mr-1 btn-height-22 white--text" block @click="clickAdd">Add</v-btn>
    </v-card-actions>
  </div>
</template>

<script>
  export default {
    props: ['items', 'type'],
    data: () => ({
      defaultTypes: [
        'string', 'number',
        'boolean', 'variable',
        'password'
      ]
    }),
    computed: {
      valueItems() {
        const { variables } = this.$store.state.setting;
        return variables.filter(v => v.type !== 'array' && v.enable).map(v => v.key);
      },
      dataItems() {
        return (type) => {
          const { variables } = this.$store.state.setting;
          const variable = variables.find(v => v.key === type);
          if (variable && variable.value) {
            return variable.value
          }
        }
      },
      typeItems() {
        const { variables } = this.$store.state.setting;
        let arrayVariables = [];
        if (variables) {
          arrayVariables = variables
            .filter(v => v.type === 'array' && v.enable)
            .map(v => v.key);
        }
        return [...this.defaultTypes, ...arrayVariables];
      }
    },
    methods: {
        onChangeValue(item) {
          item.value = '${' + item.value + '}';
        },
        onChangeType(v, item) {
          if (v === 'string' && item.value) {
              item.value = item.value.toString();
          }
        },
        clickAdd() {
          this.items.push({enable: true, type: 'string'});
        },
        clickRemove(index) {
          this.items.splice(index, 1);
        }
    }
  };
</script>
<style>
  .v-card__actions {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .wrap-remove {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .v-autocomplete__content.v-menu__content {
    text-align: left;
  }
</style>
