<template>
  <v-row>
    <v-col cols="4">
      <v-select
        @change="onChangeType" v-if="auth && auth.type"
        label="Type" v-model="auth.type"
        :items="['noAuth', 'basicAuth', 'token']"></v-select>
    </v-col>
    <v-col cols="8">
      <v-row v-if="auth && auth.type !== 'noAuth'">
        <v-col>
          <v-text-field v-model="auth.key" label="Key" ></v-text-field>
        </v-col>
      </v-row>
      <v-row v-if="auth && auth.type === 'basicAuth'">
        <v-col>
          <v-text-field v-model="auth.username" label="Username" ></v-text-field>
        </v-col>
        <v-col>
          <v-text-field v-model="auth.password" label="Password" ></v-text-field>
        </v-col>
      </v-row>
      <v-text-field v-if="auth && auth.type === 'token'" v-model="auth.token" label="Token" ></v-text-field>
    </v-col>
  </v-row>
</template>

<script>

export default {
    props: ['auth'],
    methods: {
        onChangeType() {
            if (this.auth && this.auth.type !== 'noAuth') {
                this.auth.key = 'Authorization';
            }
        }
    }
};
</script>
