<template>
  <div>
    <page-title title="Api Client" />

    <div class="text-left mb-3">
      <v-btn x-small class="mr-1 btn-height-22 white--text" color="primary" @click="clickImport('collections')">Import<v-icon x-small right>mdi-import</v-icon></v-btn>
      <v-btn x-small class="mr-1 btn-height-22 white--text" color="#009688" @click="() => clickJsonExport(collections)">Export<v-icon x-small right>mdi-export</v-icon></v-btn>
      <v-btn x-small class="mr-1 btn-height-22 white--text" color="#8cd0ab" @click="showVariableDialog">Variables<v-icon x-small right>mdi-variable</v-icon></v-btn>
      <v-btn x-small class="mr-1 btn-height-22 white--text" color="warning" @click="clickExecuteGlobal">Execute All Global<v-icon x-small right>mdi-home-automation</v-icon></v-btn>
      <v-btn x-small class="mr-1 btn-height-22 white--text" color="info" @click="saveAll">Save All Global</v-btn>
    </div>
    <v-tabs vertical v-model="collectionTab" class="collection-tabs">
      <div style="width: 280px">
        <draggable v-model="collections" @start="drag = true" @end="onDropCollection" class="left-panel">
          <v-tab
            @contextmenu="(e) => showCollectionMenu(e, index)"
            :key="index" @click.native="() => clickOnCollectionTab(index)"
            v-for="(collection, index) of collections">
            {{ collection.name }}
            <v-spacer></v-spacer>
            <v-btn @click="event => clickDeleteCollection(index, event)" class="btn-delete" icon x-small color="warning" >
              <v-icon x-small>mdi-close-circle</v-icon>
            </v-btn>
          </v-tab>
          <v-tab :key="collections.length" class="api-tab tab-add-collection" @click.native="addCollection">
            <v-icon>mdi-plus</v-icon>
          </v-tab>
        </draggable>
      </div>
      <div style="width: calc(100vw - 380px);">
        <v-card style="min-height: calc(100vh - 24px)" class="right-panel">
          <v-card-text>
            <v-tabs class="item-tab" v-model="tab" show-arrows="desktop">
              <v-tabs-slider color="teal lighten-3"></v-tabs-slider>
              <draggable
                v-if="collections[collectionTab]"
                v-model="collections[collectionTab].items"
                @start="drag = true" @end="() => onDropTab(collectionTab)">
                <v-tab
                  :style="{ color: item.enable ? '#1976d2' : 'gray' }"
                  class="api-tab text-left" :key="index"
                  v-for="(item, index) of collections[collectionTab].items"
                  @contextmenu="(e) => showTabMenu(e, collectionTab, index)"
                >
                  <span v-bind:style="{ color: methodColor[item.method] }" class="text-method">{{ item.method }}</span>&nbsp;
                  <span>{{ item.name }}</span>
                  <v-tooltip v-if="item.enableCron" bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <span v-bind="attrs" v-on="on" class="cron-icon" >C</span>
                    </template>
                    <span>Crone Expression: {{item.cronExpr}}</span>
                  </v-tooltip>
                  <v-tooltip bottom v-if="item.delay && item.delay !== '0'">
                    <template v-slot:activator="{ on, attrs }">
                      <span v-bind="attrs" v-on="on" class="delay-icon">D</span>
                    </template>
                    <span>Delay value: {{item.delay}} seconds</span>
                  </v-tooltip>

                  <v-btn @click.native="event => clickDeleteItem(collectionTab, index, event)" class="btn-delete" icon x-small color="warning" >
                    <v-icon x-small>mdi-close-circle</v-icon>
                  </v-btn>
                </v-tab>
                <v-tab class="api-tab" @click.native="addItem(collectionTab)" :key="collections[collectionTab].items.length">
                  <v-icon>mdi-plus</v-icon>
                </v-tab>
              </draggable>
            </v-tabs>
            <v-tabs-items v-if="collections[collectionTab]" v-model="tab">
              <v-tab-item :key="index" v-for="(item, index) of collections[collectionTab].items">
                <v-card flat>
                  <v-card-text>
                    <ac-tab
                      :send="item => sendRequest(collectionTab, index)"
                      :save="item => onClickSave(item, collectionTab, index)"
                      :item="item"
                      >
                    </ac-tab>
                  </v-card-text>
                </v-card>
              </v-tab-item>
            </v-tabs-items>
          </v-card-text>
        </v-card>
      </div>
    </v-tabs>

    <v-overlay :value="sending"></v-overlay>

    <v-dialog v-model="importDialog" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">Import</span>
        </v-card-title>
        <v-card-text>
          <v-alert class="text-left" dense colored-border type="error" elevation="2" v-show="disableSave && dataImport" >Incorrect Data Type!</v-alert >
          <v-container>
            <v-textarea outlined name="input-7-4" label="import data" v-model="dataImport" @input="onChangeImportData" ></v-textarea>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text @click="closeDialog">Cancel</v-btn>
          <v-btn color="primary" text :disabled="disableSave" v-on:click="saveImport">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog v-model="importVariableDialog" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">Import</span>
        </v-card-title>
        <v-card-text>
          <v-alert class="text-left" dense colored-border type="error" elevation="2" v-show="disableSave && dataImport" >Incorrect Data Type!</v-alert >
          <v-container>
            <v-textarea outlined name="input-7-4" label="import data" v-model="dataImport" @input="onChangeImportData" ></v-textarea>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text @click="importVariableDialog = false">Cancel</v-btn>
          <v-btn color="primary" text :disabled="disableSave" v-on:click="importVariable">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="collectionDialog" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">Collection Setting</span>
        </v-card-title>
        <v-card-text>
          <v-text-field placeholder="Name" v-model="editableCollection.name" label="Name" >
          </v-text-field>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text @click="closeDialog">Cancel</v-btn>
          <v-btn color="primary" text v-on:click="saveCollection">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="variablesDialog" max-width="1200px">
      <v-card>
        <v-card-title>
          <span class="headline">Global Variables</span>
        </v-card-title>
        <v-card-text>
          <div class="text-left">
            <v-btn x-small class="mr-1 btn-height-22 white--text" color="primary" @click="importVariableDialog = true">Import<v-icon x-small right>mdi-import</v-icon></v-btn>
            <v-btn x-small class="mr-1 btn-height-22 white--text" color="#009688" @click="exportVariable">Export<v-icon x-small right>mdi-export</v-icon></v-btn>
          </div>
          <ac-variable-form :type="'globalVariable'" :items="variables"></ac-variable-form>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text @click="closeDialog">Cancel</v-btn>
          <v-btn color="primary" text v-on:click="() => clickSaveVariable(variables)">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="tabVariableDialog" max-width="800px">
      <v-card>
        <v-card-title>
          <span class="headline">Tab: {{itemName}} Variables</span>
        </v-card-title>
        <v-card-text>
          <ac-variable-form :type="'tabVariable'" :items="tabVariables"></ac-variable-form>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text @click="cancelVariableDialog">Cancel</v-btn>
          <v-btn color="primary" text v-on:click="sendTabVariables">Send</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="tabDialog" max-width="600px">
      <v-card>
        <v-card-title>
          <span class="headline">Tabs Setting</span>
        </v-card-title>
        <v-card-text>
          <ac-tab-setting :item="editableTab"></ac-tab-setting>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text @click="closeDialog">Cancel</v-btn>
          <v-btn color="primary" text v-on:click="saveTab" >Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="sending" hide-overlay persistent width="300">
      <v-card color="primary" dark>
        <v-card-text style="padding: 5px">
          Sending {{itemName}}
          <v-progress-linear
            indeterminate
            color="white"
            class="mb-0"
          ></v-progress-linear>
          <v-btn x-small text @click="cancelSending">Cancel</v-btn>
        </v-card-text>
      </v-card>
    </v-dialog>
    <v-dialog v-model="confirmDialog" persistent max-width="290" >
      <v-card>
        <v-card-title class="headline">
          Confirm
        </v-card-title>
        <v-card-text>Are you sure?</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text @click="onCancelConfirm">No</v-btn>
          <v-btn color="red darken-1" text @click="onConfirm()">Yes</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-menu transition="slide-y-transition" bottom v-model="tabMenu" :position-x="tabX" :position-y="tabY" >
      <v-list class="text-left">
        <v-list-item link @click.native="showTabSetting">
          <v-icon small>mdi-square-edit-outline</v-icon>&nbsp;&nbsp;&nbsp;
          <v-list-item-title class="text-small"> Edit </v-list-item-title>
        </v-list-item>

        <v-list-item link @click="clickImport('item')">
          <v-icon small>mdi-import</v-icon>&nbsp;&nbsp;&nbsp;
          <v-list-item-title class="text-small"> Import </v-list-item-title>
        </v-list-item>

        <v-list-item link @click="() => clickJsonExport(collections[cIndex].items[selectedTabIndex])">
          <v-icon small>mdi-export</v-icon>&nbsp;&nbsp;&nbsp;
          <v-list-item-title class="text-small"> Export </v-list-item-title>
        </v-list-item>

        <v-list-item link @click="() => sendRequest(cIndex, selectedTabIndex)">
          <v-list-item-title class="text-small">
            <v-icon small>mdi-home-automation</v-icon>&nbsp;&nbsp;
            <v-tooltip bottom>
              <template v-slot:activator="{ on, attrs }">
                <span v-bind="attrs" v-on="on"> Send </span>
              </template>
              <span>Send request</span>
            </v-tooltip>
          </v-list-item-title>
        </v-list-item>

        <v-list-item link @click="onClickSave(
          collections[cIndex].items[selectedTabIndex],
          cIndex, selectedTabIndex
        )">
          <v-list-item-title class="text-small">
            <v-icon small>mdi-content-save</v-icon>&nbsp;&nbsp;
            <v-tooltip bottom>
              <template v-slot:activator="{ on, attrs }">
                <span v-bind="attrs" v-on="on"> Save </span>
              </template>
              <span>Save this collection</span>
            </v-tooltip>
          </v-list-item-title>
        </v-list-item>

        <v-list-item link @click.native="closeOtherTabs">
          <v-icon small>mdi-close-circle</v-icon>&nbsp;&nbsp;&nbsp;
          <v-list-item-title class="text-small"> Close others </v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>
    <v-menu transition="slide-y-transition" bottom v-model="collectionMenu" :position-x="collectionX" :position-y="collectionY">
      <v-list class="text-left">
        <v-list-item link @click="evt => showCollectionSetting(collectionMenuIndex, evt)">
          <v-icon small>mdi-square-edit-outline</v-icon>&nbsp;&nbsp;&nbsp;
          <v-list-item-title class="text-small"> Edit Tab Name </v-list-item-title>
        </v-list-item>

        <v-list-item link @click="clickImport('items', collectionMenuIndex)">
          <v-icon small>mdi-import</v-icon>&nbsp;&nbsp;&nbsp;
          <v-list-item-title class="text-small"> Import All Tabs </v-list-item-title>
        </v-list-item>

        <v-list-item link @click="() => clickJsonExport(collections[collectionMenuIndex].items)">
          <v-icon small>mdi-export</v-icon>&nbsp;&nbsp;&nbsp;
          <v-list-item-title class="text-small"> Export All Tabs </v-list-item-title>
        </v-list-item>

        <v-list-item link @click="sendAllTabs(collectionMenuIndex)">
          <v-list-item-title class="text-small">
            <v-icon small>mdi-home-automation</v-icon>&nbsp;&nbsp;
            <v-tooltip bottom>
              <template v-slot:activator="{ on, attrs }">
                <span v-bind="attrs" v-on="on"> Execute All Tabs </span>
              </template>
              <span>Send all request in this collection</span>
            </v-tooltip>
          </v-list-item-title>
        </v-list-item>

        <v-list-item link @click="() => saveData(collections[collectionMenuIndex])">
          <v-list-item-title class="text-small">
            <v-icon small>mdi-content-save</v-icon>&nbsp;&nbsp;
            <v-tooltip bottom>
              <template v-slot:activator="{ on, attrs }">
                <span v-bind="attrs" v-on="on"> Save All Tabs </span>
              </template>
              <span>Save this collection</span>
            </v-tooltip>
          </v-list-item-title>
        </v-list-item>
        <v-list-item link @click.native="closeOtherCollections">
          <v-icon small>mdi-close-circle</v-icon>&nbsp;&nbsp;&nbsp;
          <v-list-item-title class="text-small"> Close others </v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>
  </div>
</template>

<script>
  import AcTab from './AcTab';
  import KeyValueForm from './KeyValueForm.vue';
  import draggable from 'vuedraggable';
  import  jsonata from 'jsonata';
  import {copyToClipboard, isJsonString, getUrl, getVariableName, getWsUrl} from '../../utils/utils';
  import * as localstorageUtil from "../../utils/localstorage";
  import * as settingService from '../../services/settingService';

  import axios from '../../axios';
  import _axios from 'axios';
  import AcVariableForm from './AcVariableForm.vue';
  import AcTabSetting from "./AcTabSetting";
  import PageTitle from '../Common/PageTitle.vue';

  const defaultItem = {
    name: 'New Tab', method: 'Get',
    params: [], headers: [],
    body: [], enable: true,
    auth: {type: 'noAuth'},
    cronType: 'cronExpression'
  };

  export default {
    components: {
      AcTabSetting,
      AcTab,
      KeyValueForm,
      draggable,
      AcVariableForm,
      PageTitle
    },
    data: () => ({
      variablesDialog: false,
      importDialog: false,
      importVariableDialog: false,
      collectionDialog: false,
      tabVariableDialog: false,
      tabDialog: false,
      confirmDialog: false,
      tabMenu: false,
      collectionMenu: false,
      disableSave: true,
      methodColor: {Get: '#8cd0ab', Post: 'orange', Put: 'lightcoral', Patch: 'gray', Delete: 'red'},
      action: '',
      sending: false,
      itemName: '',
      cancelTab: false,
      timeoutValues: {},
      editableCollection: {},
      editableTab: {},

      tabVariables: [],
      dataImport: '',
      cIndex: -1,
      collectionMenuIndex: -1,
      selectedTabIndex: 0,
      varItemIndex: -1,
      itemIndex: -1,
      tab: 0,
      collectionTab: 0,
      importType: '',
      collections: [{ name: 'New', items: [defaultItem] }],
      defaultCollections: [{ name: 'New', items: [defaultItem] }],

      collectionX: 0,
      collectionY: 0,
      tabX: 0,
      tabY: 0,
    }),
    mounted() {
      this.getData();
      this.socket = new WebSocket(getWsUrl(), 'api-client');
      this.socket.addEventListener('message', async ({ data }) => {
        const { collectionId, itemId, resData } = JSON.parse(data);
        for (let i = 0; i < this.collections.length; i++) {
          const c = this.collections[i];
          if (c._id === collectionId) {
            this.cIndex = i;
            if (c && c.items) {
              for (let j = 0; j < c.items.length; j ++) {
                const item = c.items[j];
                if (item._id === itemId) {
                  this.itemIndex = j;
                  await this.onSendSuccess(item, resData);
                  this.itemIndex = -1;
                  break;
                }
              }
            }
            break;
          }
        }

      });
    },
    beforeRouteLeave (to, from, next) {
      this.socket.close();
      next();
    },
    computed: {
      variables() {
        const { setting } = this.$store.state;
        return setting.variables || [];
      }
    },
    methods: {
      async clickSaveVariable(variables) {
        await this.saveVariables(variables);
        this.variablesDialog = false;
      },
      async importVariable() {
        const { setting } = this.$store.state;
        let dataImport = JSON.parse(this.dataImport);
        let variableKeys = [];
        if (setting.variables) {
          variableKeys = setting.variables.map(v => v.key);
        }
        dataImport = dataImport.filter(x => !variableKeys.includes(x.key));
        const newVariables = [...setting.variables, ...dataImport];
        await this.saveVariables(newVariables);
        this.importVariableDialog = false;
      },
      clickExecuteGlobal() {
        this.confirmDialog = true;
        this.action = 'sendAll';
        this.cIndex = 0;
      },
      cancelSending() {
        this.cancelTab = true;
        clearTimeout(this.timeoutValues[this.cIndex][this.itemIndex]);
        this.sending = false;
      },
      clickDeleteCollection(index, e) {
        this.action = 'removeCollection';
        this.cIndex = index;
        this.confirmDialog = true;
        e.stopPropagation();
      },
      onCancelConfirm() {
        this.confirmDialog = false;
        this.cIndex = -1;
        this.action = '';
      },
      async closeOtherCollections() {
          let index = 0;
          for (const c of this.collections) {
              if (this.collectionMenuIndex !== index) {
                  await this.deleteData(c._id);
              }
              index++;
          }
          this.getData();
      },
      async closeOtherTabs() {
          let index = 0;
          const items = [];
          for (const item of this.collections[this.cIndex].items) {
              if (this.selectedTabIndex === index) {
                  items.push({...item});
              }
              index++;
          }
          this.collections[this.cIndex].items = [...items];
          await this.saveData(this.collections[this.cIndex]);
      },
      async cancelVariableDialog() {
        this.tabVariableDialog = false;
        if (this.action !== '') {
          await this[this.action]();
        }
      },
      async saveTab() {
          const item = this.collections[this.cIndex].items[this.selectedTabIndex];
          this.collections[this.cIndex].items[this.selectedTabIndex] = {...item, ...this.editableTab};
          await this.saveData(this.collections[this.cIndex]);
          this.cIndex = -1;
      },
      async sendTabVariables() {
        for (const tabVariable of this.tabVariables) {
          if (!tabVariable.value) {
            this.$toasted.show('Please fill all data to continue', {type: 'error'});
            return;
          }
        }
        const item = this.collections[this.cIndex].items[this.varItemIndex];
        this.itemIndex = this.varItemIndex;
        await new Promise(resolve => this.send(item, resolve));
        this.tabVariableDialog = false;
        if (this.action !== '') {
          await this[this.action]();
        }
      },
      clickOnCollectionTab(index) {
        this.tab = 0;
        this.collectionTab = index;
      },
      async sendAll() {
        for (let i = 0; i < this.collections.length; i++) {
          if (i < this.cIndex || (i === this.cIndex && this.itemIndex === this.collections[i].items.length)) {
            this.itemIndex = -1;
            continue;
          }
          this.cIndex = i;
          const result = await this.sendAllInCollection();
          if (!result) {
            this.action = 'sendAll';
            return;
          }
        }
        this.cIndex = -1;
        this.itemIndex = -1;
        this.varItemIndex = -1;
      },
      async sendAllInCollection() {
        const { items } = this.collections[this.cIndex];
        let index = 0;
        for (const item of items) {
          if (!item.enable || !item.url || this.cancelTab || this.varItemIndex >= index) {
            index++;
            continue;
          }
          this.itemIndex++;
          this.tabVariables = this.checkVariable(item);
          this.itemName = item.name;
          if (this.tabVariables.length > 0) {
            this.tabVariableDialog = true;
            this.varItemIndex = index;
            return false;
          }
          await new Promise(resolve => this.send(item, resolve));
          index++;
        }
        this.varItemIndex = -1;
        this.itemIndex = -1;
        return true;
      },
      async onConfirm() {
        this.confirmDialog = false;
        this.cancelTab = false;
        await this[this.action]();
        this.itemIndex = -1;
      },
      clickDeleteItem(cIndex, index, event) {
        this.cIndex = cIndex;
        this.confirmDialog = true;
        this.itemIndex = index;
        this.action = 'removeItem';
        event.stopPropagation();
      },
      showCollectionSetting(index, evt) {
        this.collectionMenu = false;
        this.cIndex = index;
        this.collectionDialog = true;
        this.editableCollection = {name: this.collections[index].name};
        evt.stopPropagation();
      },
      showTabSetting(evt) {
        this.tabDialog = true;
        const item = this.collections[this.cIndex].items[this.selectedTabIndex];
        const { enableCron, cronExpr, cronType, intervalType, minute, hour, day, month, dayBetweenStart, dayBetweenEnd } = item;
        this.editableTab = {
          name: item.name,
          delay: item.delay,
          enableCron, cronExpr,
          cronType, intervalType,
          minute, hour, day, month,
          dayBetweenStart, dayBetweenEnd
        };
        evt.stopPropagation();
        this.tabMenu = false;
      },
      async onDropCollection() {
        this.drag = false;
        let index = 0;
        for (const collection of this.collections) {
          collection.index = index;
          await this.saveData(collection);
          index++;
        }
      },
      async onDropTab(collectionTab, test) {
        this.drag = false;
        let index = 0;
        for (const item of this.collections[collectionTab].items) {
          item.index = index;
          index++;
        }
        await this.saveData(this.collections[collectionTab]);
      },
      async saveCollection() {
          this.collections[this.cIndex] = {...this.collections[this.cIndex], ...this.editableCollection};
          await this.saveData(this.collections[this.cIndex]);
          this.cIndex = -1;
      },
      async saveAll() {
        for (const collection of this.collections) {
          await this.saveData(collection);
        }
        this.getData();
      },
      sendAllTabs(index) {
        this.confirmDialog = true;
        this.cIndex = index;
        this.action = 'sendAllInCollection';
      },
      checkVariable(item) {
        let variableKeys = [];
        const { variables: totalVariables } = this.$store.state.setting;
        const variables = {};
        if (totalVariables) {
          totalVariables.filter(v => v.enable && v.type !== 'array').forEach(v => {
            if (v.key && v.value) {
              variables[v.key] = v;
            }
          });
        }

        for (const data of item.headers) {
          if (data.key && data.value && data.enable) {
            const keys = getVariableName(data.key);
            const values = getVariableName(data.value);
            for (const key of keys) {
              variableKeys.push(key);
            }
            for (const value of values) {
              variableKeys.push(value);
            }
          }
        }

        for (const data of item.params) {
          if (data.key && data.value && data.enable) {
            const keys = getVariableName(data.key);
            const values = getVariableName(data.value);
            for (const key of keys) {
              variableKeys.push(key);
            }
            for (const value of values) {
              variableKeys.push(value);
            }
          }
        }

        const urlVariables = getVariableName(item.url);

        for (const key of urlVariables) {
          variableKeys.push(key);
        }

        for (const data of item.body) {

          if (data.key && data.value && data.enable) {
            const keys = getVariableName(data.key);
            const values = getVariableName(data.value);
            for (const key of keys) {
              variableKeys.push(key);
            }
            for (const value of values) {
              variableKeys.push(value);
            }
          }
        }

        const result = [];

        variableKeys = variableKeys.filter(function(item, pos) { return variableKeys.indexOf(item) == pos });
        for (const variableKey of variableKeys) {
          if (variables[variableKey] && variables[variableKey].useDefault) {
            continue;
          }
          let variableValue = '';
          let variableType = 'string';
          if (variables[variableKey] && variables[variableKey].value) {
            variableValue = variables[variableKey].value;
            variableType = variables[variableKey].type;
          }
          result.push({key: variableKey, value: variableValue, type: variableType, enable: true})
        }
        return result;
      },
      getVarValue(str) {
        let { variables: settingVariables } = this.$store.state.setting;
        const tabVariableKeys = this.tabVariables.map(t => {
            if (t.enable) {
                return t.key;
            }
        });

        let variables = [...this.tabVariables];

        if (settingVariables) {
          settingVariables = settingVariables.filter(s => {
              return s.key && s.value && s.enable && !tabVariableKeys.includes(s.key) && s.type !== 'array';
          });
          variables = [...this.tabVariables, ...settingVariables];
        }


        const variableObj = {};
        if (variables) {
          for (const variable of variables) {
            if (variable.key && variable.value && variable.enable) {
              variableObj[variable.key] = variable;
            }
          }
        }
        const varNames = getVariableName(str);
        for (const varName of varNames) {
          if (variableObj[varName]) {
            let value = variableObj[varName].value;
            if (variableObj[varName].type === 'boolean') {
              value = value === 'true';
            }
            if (variableObj[varName].type === 'number') {
              value = Number(value);
            }

            str = str.replace('${' + varName + '}', value);
          }
        }
        return str;
      },
      async sendRequest(cIndex, index) {
        this.cIndex = cIndex;
        const item = this.collections[cIndex].items[index];
        this.itemIndex = index;
        if (!item.url) {
          return;
        }
        this.tabVariables = this.checkVariable(item);
        if (this.tabVariables.length > 0) {
          this.tabVariableDialog = true;
          this.itemName = item.name;
          this.varItemIndex = index;
          return false;
        }
        await new Promise(resolve => this.send(item, resolve));
      },
      getRequestData(items) {
        const result = {};
        for (const item of items) {
          if (item.key && item.value && item.enable) {
            let value = item.value;
            if (item.type === 'boolean') {
              value = value === 'true';
            }
            if (item.type === 'string' || item.type === 'variable') {
              value = this.getVarValue(value);
            }
            if (item.type === 'number') {
              value = Number(value);
            }
            const key = this.getVarValue(item.key);
            result[key] = value;
          }
        }
        return result;
      },
      async send(item, resolve) {
        const { auth } = item;

        const headers = this.getRequestData(item.headers);
        const params = this.getRequestData(item.params);
        const body = this.getRequestData(item.body);

        if (auth) {
            if (auth.type === 'basicAuth' && auth.username && auth.password && auth.key) {
              const username = this.getVarValue(auth.username);
              const password = this.getVarValue(auth.password);
              headers[auth.key] = `Basic ${username}:${password}`;
            }

            if (auth.type === 'token' && auth.token && auth.key) {
                headers[auth.key] = this.getVarValue(auth.token);
            }
        }

        const url = this.getVarValue(item.url);
        const method = item.method.toLocaleLowerCase();
        const data = { url, method, headers, body, params };
        const token = localstorageUtil.getItem('token');
        const config = { headers: {
            'Authorization': 'Bearer ' + token
        }};

        this.sending = true;
        this.itemName = item.name;
        const delayValue = item.delay ? item.delay*1000: 0;

        const _this = this;
        if (!this.timeoutValues[this.cIndex]) {
          this.timeoutValues[this.cIndex] = {};
        }
        this.timeoutValues[this.cIndex][this.itemIndex] = setTimeout(async function () {
          try {
            let {data: resData} = await _axios.post(`${getUrl()}/api/api-client/send`, data, config);
            await _this.onSendSuccess(item, resData);
            _this.$toasted.show(`${item.name} success!`);
            _this.sending = false;
            resolve();
          } catch (error) {
            _this.sending = false;
            _this.onSendFail(error, item);
            resolve();
          }
        }, delayValue);
      },
      onSendFail({response, message}, item) {
        let errMsg = message;
        if (response) {
          errMsg = response.data;
        }
        if (this.collections[this.cIndex].items[this.itemIndex]) {
          this.collections[this.cIndex].items[this.itemIndex] = {
            ...this.collections[this.cIndex].items[this.itemIndex],
            result: errMsg
          };
        }

        this.$toasted.show(`${item.name} failed!`, { type: 'error' });
      },
      async onSendSuccess(item, resData) {
        if (item.filter) {
          const expression = jsonata(item.filter);
          resData = expression.evaluate(resData);
        }
        if (this.collections[this.cIndex].items[this.itemIndex]) {
          this.collections[this.cIndex].items[this.itemIndex] = {
            ...this.collections[this.cIndex].items[this.itemIndex],
            result: resData
          };
        }
        let variables = this.variables ? [...this.variables]: [];
        if (item.setAsVariable && item.variableName) {
          const variable = {
            enable: true, useDefault: true,
            key: item.variableName,
            value: resData,
            type: 'string'
          };
          variables = variables.filter(v => v.key !== item.variableName);
          variables.push(variable);
          await this.saveVariables(variables);
        }
      },
      async onClickSave(item, cIndex, itemIndex) {
        this.collections[cIndex].items[itemIndex] = item;
        const r = await this.saveData(this.collections[cIndex]);
        await this.getData();
        this.$toasted.show(r.status);
      },
      async saveVariables(variables) {
        const { setting } = this.$store.state;
        setting.variables = variables;
        const result = await settingService.update(setting);
        if (result === 'successful') {
          this.$store.commit('changeSetting', setting);
          this.$toasted.show('Success!');
        }
      },
      showVariableDialog() {
        this.variablesDialog = true;
      },
      exportVariable(){
        const { variables } = this.$store.state.setting;
        const status = copyToClipboard(JSON.stringify(variables), 'v-dialog');
        const msg = status ? `Copied to clipboard!` : `Failure on copying on clipboard!`;
        const type = status ? `success` : `error`;
        return this.$toasted.show(msg, { type: type });
      },
      async onChangeImportData() {
        this.disableSave = !isJsonString(this.dataImport);
      },
      clickJsonExport(data) {
        const status = copyToClipboard(JSON.stringify(data));
        const msg = status ? `Copied to clipboard!` : `Failure on copying on clipboard!`;
        const type = status ? `success` : `error`;
        return this.$toasted.show(msg, { type: type });
      },
      clickImport(type, index) {
        this.importDialog = true;
        this.importType = type;
        if (type === 'items') {
          this.cIndex = index;
        }
      },
      async saveImport() {
        if (this.importType === 'items') {
          const items = JSON.parse(this.dataImport);
          this.collections[this.cIndex].items = [
            ...this.collections[this.cIndex].items,
            ...items,
          ];
          this.saveData(this.collections[this.cIndex]);
        }
        if (this.importType === 'collections') {
          const collections = JSON.parse(this.dataImport);
          for (let i = 0; i < collections.length; i++) {
            const data = { ...collections[i] };
            delete data._id;
            this.saveData(data);
          }
        }
        if (this.importType === 'item') {
          const item = JSON.parse(this.dataImport);
          this.onClickSave({...item}, this.cIndex, this.selectedTabIndex)
        }
        this.importDialog = false;
        this.getData();
      },
      closeDialog() {
        this.variablesDialog = false;
        this.importDialog = false;
        this.collectionDialog = false;
        this.tabDialog = false;
        this.cIndex = -1;
      },
      async removeItem() {
        const collection = this.collections[this.cIndex];
        collection.items.splice(this.itemIndex, 1);
        this.saveData(collection);
      },
      async removeCollection() {
        const collection = this.collections[this.cIndex];
        if (collection && collection._id) {
          await this.deleteData(collection._id);
        }
        this.collections.splice(this.cIndex, 1);
      },
      async deleteData(id) {
        const r = await axios.delete(`api-client/${id}`);
        this.$toasted.show(r.status);
      },
      async saveData(data) {
        const r = await axios.put('api-client', data);
        this.closeDialog();
        return r;
      },
      addItem(index) {
        const item = JSON.parse(JSON.stringify(defaultItem));
        this.collections[index].items.push(item);
        this.tab = this.collections[index].items.length - 1;
      },
      async addCollection(e) {
        const collection = JSON.parse(JSON.stringify(this.defaultCollections[0]));
        this.collections.push(collection);
        this.collectionTab = this.collections.length - 1;
        this.tab = 0;
        await this.saveAll();
      },
      async getData() {
        const collections = await axios.get('api-client');
        for (const collection of collections) {
          if (!collection.items) {
            collection.items = [defaultItem].slice();
          }
        }
        this.collections = collections.length === 0 ? [...this.defaultCollections] : collections;
      },
      showTabMenu (e, cIndex, tabIndex) {
        this.cIndex = cIndex;
        this.selectedTabIndex = tabIndex;
        e.preventDefault();
        this.tabMenu = false;
        this.collectionMenu = false;
        this.tabX = e.clientX;
        this.tabY = e.clientY;
        this.$nextTick(() => {
          this.tabMenu = true
        })
      },
      showCollectionMenu (e, index) {
          e.preventDefault();
          this.collectionMenuIndex = index;
          this.collectionMenu = false;
          this.tabMenu = false;
          this.collectionX = e.clientX;
          this.collectionY = e.clientY;
          this.$nextTick(() => {
              this.collectionMenu = true
          })
      },
    },
  };
</script>
