<template>
  <div>
    <v-card-text>
      <v-row :key="index" v-for="(data, index) of items">
        <v-col class="wrap-remove" cols="1">
          <v-checkbox v-model="data.enable" />
        </v-col>
        <v-col v-if="type === 'globalVariable'" class="wrap-remove" cols="2">
          <v-checkbox label="Default?" v-model="data.useDefault" />
        </v-col>
        <v-col :cols="type === 'globalVariable' ? 2: 3">
          <v-text-field v-model="data.key" label="Key" ></v-text-field>
        </v-col>
        <v-col :cols="type === 'globalVariable' ? 3: 4">
          <v-text-field
            :type="data.type === 'number' || data.type === 'password' ? data.type: ''"
            v-if="data.type !== 'boolean' && data.type !== 'array'"
            v-model="data.value"
            label="Value" ></v-text-field>
          <v-select :label="'Value'" v-if="data.type === 'boolean'" v-model="data.value" :items="['true', 'false']"></v-select>

          <v-combobox
            v-if="data.type === 'array'"
            v-model="data.value"
            hide-selected
            multiple small-chips
          >
          </v-combobox>

        </v-col>
        <v-col cols="1">
          <v-select @change="(v) => onChangeType(v, data)" v-model="data.type" :items="dataItems"></v-select>
        </v-col>
        <v-col cols="2">
          <v-text-field v-model="data.description" label="Description" ></v-text-field>
        </v-col>
        <v-col class="wrap-remove" cols="1">
          <v-btn icon @click="clickRemove(index)">
            <v-icon>mdi-close-circle</v-icon>
          </v-btn>
        </v-col>
      </v-row>
    </v-card-text>
    <v-card-actions>
      <v-btn x-small class="mr-1 btn-height-22 white--text" block @click="clickAdd">Add</v-btn>
    </v-card-actions>
  </div>
</template>

<script>
export default {
  props: ['items', 'type'],
  data: () => ({
      dataItems: ['string', 'number', 'boolean', 'password', 'array']
  }),
  methods: {
      onChangeType(v, data) {
          if (v === 'string' && data.value) {
              data.value = data.value.toString();
          }
          if (v === 'array' && data.value) {
              data.value = [data.value];
          }
      },
      clickAdd() {
        this.items.push({enable: true, type: 'string'});
      },
      clickRemove(index) {
        this.items.splice(index, 1);
      }
  }
};
</script>
<style>
  .v-card__actions {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .wrap-remove {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .v-autocomplete__content.v-menu__content {
    text-align: left;
  }
</style>
