<template>
  <div>
    <v-text-field placeholder="Name" label="Name" v-model="item.name"></v-text-field>
    <v-text-field type="number" v-model="item.delay" label="Delay Value (Second)" ></v-text-field>
    <v-switch v-model="item.enableCron" label="Enable cron?"></v-switch>
    <v-radio-group v-if="item.enableCron" v-model="item.cronType" row>
      <v-radio label="Cron Expression" value="cronExpression"></v-radio>
      <v-radio label="Cron Interval" value="cronInterval"></v-radio>
    </v-radio-group>
    <v-text-field v-if="item.enableCron && item.cronType === 'cronExpression'" v-model="item.cronExpr" label="Cron Expression" ></v-text-field>
    <div v-if="item.enableCron && item.cronType === 'cronInterval'">
      <v-select :label="'Interval type'" v-model="item.intervalType" :items="intervalTypes"></v-select>
      <v-row>
        <v-col v-if="item.intervalType === 7">
          <v-text-field type="number" v-model="item.dayBetweenStart" label="Day Start" ></v-text-field>
        </v-col>
        <v-col v-if="item.intervalType === 7">
          <v-text-field type="number" v-model="item.dayBetweenEnd" label="Day End" ></v-text-field>
        </v-col>
        <v-col v-if="item.intervalType === 6">
          <v-text-field type="number" v-model="item.month" label="Month" ></v-text-field>
        </v-col>
        <v-col v-if="item.intervalType === 2 || item.intervalType === 4 || item.intervalType === 6 || item.intervalType === 7">
          <v-text-field type="number" v-model="item.day" label="Set day" ></v-text-field>
        </v-col>
        <v-col v-if="item.intervalType !== 0 && item.intervalType !== 3 && item.intervalType !== 5">
          <v-text-field type="number" v-model="item.hour" label="Set Hour" ></v-text-field>
        </v-col>
        <v-col v-if="item.intervalType !== 3 && item.intervalType !== 5">
          <v-text-field type="number" v-model="item.minute" label="Set Minute" ></v-text-field>
        </v-col>
      </v-row>

    </div>
  </div>
</template>

<script>
  export default {
    props: ['item'],
    data: () => ({
      intervalTypes: [
        { value: 0, text: 'Every Minutes (At every xth minute)' },
        { value: 1, text: 'Every Hours (At minute x past every xth hour.)' },
        { value: 2, text: 'Every Days (At x:xx every x days)' },
        { value: 8, text: 'Every Day At Time (x:xx)' },
        { value: 3, text: 'Every Month (Start of every month)' },
        { value: 4, text: 'Every Month On (On x Day at x:xx)' },
        { value: 5, text: 'Every Year (Start of every year)' },
        { value: 6, text: 'Every Year On (On x month x day at x:xx)' },
        { value: 7, text: 'Between Days (At x:xx between x and x day of the month)' },
      ]
    }),
    watch: {
      item: {
        handler(val, oldVal) {
          const {
            enableCron, minute, hour,
            intervalType, day, month,
            dayBetweenStart, dayBetweenEnd,
            cronType
          } = val;
          if (cronType === 'cronExpression') {
            return;
          }
          let cronExpr = '';
          if (enableCron) {
            if (intervalType === 0 && minute >= 0 && minute < 60) {
              cronExpr = '*/' + minute + ' * * * *';
            } else if (intervalType === 1 && minute >= 0 && minute < 60 && hour >= 0 && hour < 24) {
              cronExpr = minute + ' */' + hour + ' * * *';
            } else if (intervalType === 2 && minute >= 0 && minute < 60 && hour >= 0 && hour < 24 && day > 0 && day <= 31) {
              cronExpr = minute + ' ' + hour + ' */' + day + ' * *';
            } else if (intervalType === 3) {
              cronExpr = '0 0 1 * *';
            } else if (intervalType === 4 && minute >= 0 && minute < 60 && hour >= 0 && hour < 24 && day > 0 && day <= 31) {
              cronExpr = minute + ' ' + hour + ' ' + day + ' * *';
            } else if (intervalType === 5) {
              cronExpr = '0 0 1 1 *';
            } else if (
              intervalType === 6 &&
              minute >= 0 &&
              minute < 60 &&
              hour >= 0 &&
              hour < 24 &&
              day > 0 &&
              day <= 31 &&
              month >= 0 &&
              month <= 12
            ) {
              cronExpr = minute + ' ' + hour + ' ' + day + ' ' + month + ' *';
            } else if (
              intervalType === 7 &&
              minute >= 0 &&
              minute < 60 &&
              hour >= 0 &&
              hour < 24 &&
              dayBetweenStart < dayBetweenEnd &&
              dayBetweenStart >= 1 &&
              dayBetweenStart <= 30 &&
              dayBetweenEnd >= 2 &&
              dayBetweenEnd <= 31
            ) {
              cronExpr = minute + ' ' + hour + ' ' + dayBetweenStart + '-' + dayBetweenEnd + ' * *';
            } else if (intervalType === 8 && minute >= 0 && minute < 60 && hour >= 0 && hour < 24) {
              cronExpr = minute + ' ' + hour + ' * * *';
            }
          }
          this.item.cronExpr = cronExpr;
        },
        deep: true
      }
    }
  };
</script>
